const theme = createTheme({
  palette: {
    mode: "dark",
    primary: {
      main: "#90caf9",
    },
    secondary: {
      main: "#f48fb1",
    },
    background: {
      default: "#212121",
      paper: "#424242",
    },
  },
});

function App() {
  const taskStatus = useStore((state) => state.taskStatus);
  const updateTaskStatus = useStore((state) => state.updateTaskStatus);
  const toggleRerender = useStore((state) => state.toggleRerender);

  function resetDailyValues() {
    const updated = _.each(taskStatus, (item) => {
      item.dailies = _.mapValues(item.dailies, () => false);
    });
    updateTaskStatus(updated);
    toggleRerender();
  }
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Container component="main">
        <Paper
          variant="outlined"
          sx={{ my: { xs: 3, md: 6 }, p: { xs: 2, md: 3 } }}
        >
          <Typography component="h1" variant="h4" align="center">
            Lost Ark Dailies/Weeklies Checklist
          </Typography>
          <Grid container justifyContent="center">
            <Button
              color="error"
              variant="contained"
              onClick={() => resetDailyValues()}
            >
              Reset Daily Tasks
            </Button>
          </Grid>
          <Checklist data={taskStatus} useStore={useStore} theme={theme} />
        </Paper>
      </Container>
    </ThemeProvider>
  );
}

export default App;
