function ChecklistHead(props) {
  return (
    <TableHead>
      <TableRow>
        <TableCell>
          {/* <Checkbox /> */}
          Characters
        </TableCell>
        <TableCell>
          <CharacterSelect slot={0} useStore={props.useStore} />
        </TableCell>
        <TableCell>
          <CharacterSelect slot={1} useStore={props.useStore} />
        </TableCell>
        <TableCell>
          <CharacterSelect slot={2} useStore={props.useStore} />
        </TableCell>
        <TableCell>
          <CharacterSelect slot={3} useStore={props.useStore} />
        </TableCell>
        <TableCell>
          <CharacterSelect slot={4} useStore={props.useStore} />
        </TableCell>
        <TableCell>
          <CharacterSelect slot={5} useStore={props.useStore} />
        </TableCell>
      </TableRow>
    </TableHead>
  );
}

function CharacterSelect(props) {
  const { slot } = props;
  const useStore = props.useStore;
  const taskStatus = useStore((state) => state.taskStatus);
  const updateTaskStatus = useStore((state) => state.updateTaskStatus);

  function handleChangeClass(event) {
    console.log("taskStatus:", taskStatus);
    const updated = taskStatus.map((item, i) => {
      if (slot === i) {
        return { ...item, class: event.target.value };
      } else {
        return item;
      }
    });
    updateTaskStatus(updated);
  }

  function renderMenuItems() {
    const classes = [
      "Artillerist",
      "Bard",
      "Berserker",
      "Deadeye",
      "Deathblade",
      "Gunlancer",
      "Gunslinger",
      "Paladin",
      "Scrapper",
      "Shadowhunter",
      "Sharpshooter",
      "Sorceress",
      "Soulfist",
      "Striker",
      "Summoner",
      "Wardancer",
    ];

    return classes.map((characterName) => (
      <MenuItem value={characterName} key={characterName}>
        {characterName}
      </MenuItem>
    ));
  }

  return (
    <React.Fragment>
      {/* <InputLabel id={`character-${slot}-label`}>Character</InputLabel> */}
      <Select
        labelId={`character-${slot}-label`}
        id={`character-${slot}`}
        value={useStore((state) => state.taskStatus[slot].class)}
        label={`Class`}
        onChange={handleChangeClass}
        autoWidth
      >
        {renderMenuItems()}
      </Select>
    </React.Fragment>
  );
}

function createData(name, id) {
  return { name, id };
}

const dailies = [
  createData(`Una's Task 1`, "una1"),
  createData(`Una's Task 2`, "una2"),
  createData(`Una's Task 3`, "una3"),
  createData(`Chaos Dungeon 1`, "chaos1"),
  createData(`Chaos Dungeon 2`, "chaos2"),
  createData(`Guardian Raid 1`, "guardian1"),
  createData(`Guardian Raid 2`, "guardian2"),
  createData(`Kalthertz`, "kalthertz"),
];

export default function Checklist(props) {
  const { data, useStore } = props;
  const taskStatus = useStore((state) => state.taskStatus);
  const updateTaskStatus = useStore((state) => state.updateTaskStatus);

  function handleChangeName(event, slot) {
    const updated = taskStatus.map((item, i) => {
      if (slot === i) {
        return { ...item, name: event.target.value };
      } else {
        return item;
      }
    });
    updateTaskStatus(updated);
  }

  function handleDailyStatus(id, slot) {
    // console.log(`toggle ${id} in slot ${slot}`);
    const updated = taskStatus.map((item, i) => {
      if (slot === i) {
        return {
          ...item,
          dailies: {
            ...item.dailies,
            [id]: !item.dailies[id],
          },
        };
      } else {
        return item;
      }
    });
    updateTaskStatus(updated);
  }

  return (
    <TableContainer>
      <Table sx={{ minWidth: 750 }}>
        <ChecklistHead useStore={useStore} data={data} />
        <TableBody>
          <TableRow hover key={"characterName"}>
            <TableCell>
              <Typography>Name (Optional)</Typography>
            </TableCell>
            {data.map((charData, slot) => (
              <TableCell size="medium" key={`${charData.name}-${slot}-name`}>
                <TextField
                  id={`characterName-${slot}`}
                  value={charData.name}
                  onChange={(event) => handleChangeName(event, slot)}
                  variant="standard"
                />
              </TableCell>
            ))}
          </TableRow>
          {dailies.map((row) => (
            <TableRow hover role="checkbox" key={row.id}>
              <TableCell>
                <Typography>{row.name}</Typography>
              </TableCell>
              {data.map((charData, slot) => (
                <TableCell
                  padding="checkbox"
                  key={`${charData.name}-${slot}-${row.id}`}
                >
                  <Checkbox
                    color="primary"
                    onChange={(event) => handleDailyStatus(row.id, slot)}
                    checked={data[slot].dailies[row.id]}
                  />
                </TableCell>
              ))}
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
